<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="工站">
          <el-select
            v-model="listQuery.work_id"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialog'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>包装管理列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增包装管理弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="工站">
          <el-select
            v-model="dialogForm.work_id"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialog'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="包装数量" prop="pick_number">
          <el-input v-model="dialogForm.pick_number"></el-input>
        </el-form-item>

        <el-form-item label="备注" prop="description">
          <el-input type="textarea" :rows="3" v-model="dialogForm.description"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "UserList",
  components: {
    ELTable,
  },
  //   watch: {
  //     CurrentUserInfo: function (val, oldVal) {
  //       //当val变更之后在执行接下来的操作
  //       console.log(" val val val val val",val);
  //       console.log(" oldVal oldVal oldVal oldVal oldVal",oldVal);
  //     },
  //   },
  data() {
    return {
      // 筛选
      listQuery: {
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增包装管理弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        pick_code: "",
        work_id: "",
        pick_number: "",
        description: "",
      },
      dialogFormBase: {
        pick_code: "",
        work_id: "",
        pick_number: "",
        description: "",
      },
      dialogFormRules: {
        work_id: [{ required: true, message: "请选择工站", trigger: "change" }],
        pick_number: [{ required: true, message: "请输入包装数量", trigger: "blur" }],
      },
      // 表格列
      tableCol: [
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "包装数量",
          field: "pick_number",
          align: "center",
        },
        {
          label: "备注",
          field: "description",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState(["WorkCodeNameList", "CurrentUserInfo"]),
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticPick(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticPick(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticPick(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticPick(this.listQuery);
    },
    // 包装管理列表 查询
    async getLogisticPick(params) {
      // 工站内码和工站名称接口： 查询
      await this.getLogisticWorkCodeName();

      baseManagement.getLogisticPick(params).then((res) => {
        console.log("包装管理列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.work_id == CodeNameListItem.work_id) {
                tableDataItem.work_name = CodeNameListItem.work_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 包装管理列表 新增
    postLogisticPick(params) {
      baseManagement.postLogisticPick(params).then((res) => {
        console.log("包装管理列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPick(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 包装管理列表 新增
    addBtn() {
      this.dialogTitle = "新增包装管理";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 包装管理列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改包装管理";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.dialogForm = {
        pick_code: rowData.pick_code,
        work_id: rowData.work_id,
        pick_number: rowData.pick_number,
        description: rowData.description,
      };
    },

    // 包装管理列表 修改
    getLogisticPutPick(params) {
      baseManagement.getLogisticPutPick(params).then((res) => {
        console.log("包装管理列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPick(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增包装管理") {
            console.log("新增包装管理", this.dialogForm);
            this.postLogisticPick(this.dialogForm);
          } else if (this.dialogTitle == "修改包装管理") {
            console.log("修改包装管理", this.dialogForm);
            this.getLogisticPutPick(this.dialogForm);
          }
        }
      });
    },

    // 包装管理列表 删除
    getLogisticDeletePick(params) {
      baseManagement.getLogisticDeletePick(params).then((res) => {
        console.log("包装管理列表 删除", res);
        if (res.code == 0) {
          this.getLogisticPick(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeletePick({ pick_code: rowData.pick_code });
      });
    },
  },
  mounted() {
    // 包装管理列表 查询
    this.getLogisticPick(this.listQuery);
  },
};
</script>

